import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router'
import GoogleButton from 'react-google-button'
import Paper from 'material-ui/Paper'
import Snackbar from 'material-ui/Snackbar'
import { connect } from 'react-redux'
import {
  firebaseConnect,
  isLoaded,
  isEmpty,
  pathToJS
} from 'react-redux-firebase'
import { UserIsNotAuthenticated } from 'utils/router'
import { PROFILE_NEW_PATH, LOGIN_PATH } from 'constants'
import SignupForm from '../components/SignupForm'
import ButtonOauth from 'components/ButtonOauth'
import classes from './SignupContainer.scss'


const renderPrivacyPolicy = (
  <div className={classes.containerPrivacyPolicy}>
    <h2>Privacy Policy</h2>
    <p> Kei Oka built the WizPaz app as a Free app. This SERVICE is provided by Kei Oka at no cost and is intended for use as is.</p>
    <p>This page is used to inform website visitors regarding my policies with the collection, use, and
      disclosure of Personal Information if anyone decided to use my Service.
    </p>
    <p>If you choose to use my Service, then you agree to the collection and use of information in relation
      to this policy. The Personal Information that I collect is used for providing and improving the
      Service. I will not use or share your information with anyone except as described
      in this Privacy Policy.
    </p>
     <p>The terms used in this Privacy Policy have the same meanings as in our Terms and Conditions, which is accessible
      at WizPaz unless otherwise defined in this Privacy Policy.
    </p>
     <p><strong>Information Collection and Use</strong></p>
     <p>For a better experience, while using our Service, I may require you to provide us with certain personally identifiable information, including but not limited to email address. The information that I request is retained on your device and is not collected by me in any way
     </p>
     <p>The app does use third party services that may collect information used to identify you.</p>
     <p>Link to privacy policy of third party service providers used by the app </p>
     <ul>
       <li><a href="https://firebase.google.com/policies/analytics" target="_blank">Firebase Analytics</a></li>
     </ul>
     <p><strong>Log Data</strong></p> <p> I want to inform you that whenever you use my Service, in a case of an
      error in the app I collect data and information (through third party products) on your phone
      called Log Data. This Log Data may include information such as your device Internet Protocol (“IP”) address,
      device name, operating system version, the configuration of the app when utilizing my Service,
      the time and date of your use of the Service, and other statistics.
    </p> <p><strong>Cookies</strong></p> <p>Cookies are files with small amount of data that is commonly used an anonymous unique identifier. These
      are sent to your browser from the website that you visit and are stored on your device internal memory.
    </p> <p>This Service does not use these “cookies” explicitly. However, the app may use third party code and libraries
      that use “cookies” to collection information and to improve their services. You have the option to either
      accept or refuse these cookies and know when a cookie is being sent to your device. If you choose to
      refuse our cookies, you may not be able to use some portions of this Service.
    </p> <p><strong>Service Providers</strong></p> <p> I may employ third-party companies and individuals due to the following reasons:</p> <ul><li>To facilitate our Service;</li> <li>To provide the Service on our behalf;</li> <li>To perform Service-related services; or</li> <li>To assist us in analyzing how our Service is used.</li></ul> <p> I want to inform users of this Service that these third parties have access to your
      Personal Information. The reason is to perform the tasks assigned to them on our behalf. However, they
      are obligated not to disclose or use the information for any other purpose.
    </p> <p><strong>Security</strong></p> <p> I value your trust in providing us your Personal Information, thus we are striving
      to use commercially acceptable means of protecting it. But remember that no method of transmission over
      the internet, or method of electronic storage is 100% secure and reliable, and I cannot guarantee
      its absolute security.
    </p> <p><strong>Links to Other Sites</strong></p> <p>This Service may contain links to other sites. If you click on a third-party link, you will be directed
      to that site. Note that these external sites are not operated by me. Therefore, I strongly
      advise you to review the Privacy Policy of these websites. I have no control over
      and assume no responsibility for the content, privacy policies, or practices of any third-party sites
      or services.
    </p> <p><strong>Children’s Privacy</strong></p> <p>These Services do not address anyone under the age of 13. I do not knowingly collect
      personally identifiable information from children under 13. In the case I discover that a child
      under 13 has provided me with personal information, I immediately delete this from
      our servers. If you are a parent or guardian and you are aware that your child has provided us with personal
      information, please contact me so that I will be able to do necessary actions.
    </p> <p><strong>Changes to This Privacy Policy</strong></p> <p> I may update our Privacy Policy from time to time. Thus, you are advised to review
      this page periodically for any changes. I will notify you of any changes by posting
      the new Privacy Policy on this page. These changes are effective immediately after they are posted on
      this page.
    </p> <p><strong>Contact Us</strong></p> <p>If you have any articles or suggestions about my Privacy Policy, do not hesitate to contact
      me.</p>
    </div>
)

@UserIsNotAuthenticated // redirect to list page if logged in
@firebaseConnect()
@connect(({ firebase }) => ({
  authError: pathToJS(firebase, 'authError')
}))
export default class Signup extends Component {
  static propTypes = {
    firebase: PropTypes.object,
    authError: PropTypes.object
  }

  state = {
    snackCanOpen: false,
    isOpenPrivacyPolicy: false,
  }

  handleSignup = (creds) => {
    this.setState({
      snackCanOpen: true
    })
    this.props.firebase.createUser(creds, {
      email: creds.email,
      username: creds.username
    })

  }

  providerLogin = provider => {
    this.setState({
      snackCanOpen: true
    })

    this.props.firebase
      .login({ provider, type: 'popup', scopes: ['user_friends', 'public_profile'] })
      .then(account =>
        this.props.router.push(PROFILE_NEW_PATH)
      )
  }

  onClickPrivacyPolicy = () => {
    this.setState({ isOpenPrivacyPolicy: !this.state.isOpenPrivacyPolicy })
  }

  render() {
    const { snackCanOpen } = this.state
    const { authError } = this.props

    return (
      <div className={classes.container}>
        <div className={classes.panel}>
          {/* <SignupForm onSubmit={this.handleSignup} /> */}
          {/* <div className={classes.or}>
            or
          </div> */}
          <div className={classes.providers}>
            <ButtonOauth
              className={classes.btnTwitter}
              text='Signup'
              provider='twitter'
              onClick={() => this.providerLogin('twitter')}
            />
            <ButtonOauth
              className={classes.btnTwitter}
              provider='facebook'
              text='Signup'
              onClick={() => this.providerLogin('facebook')}
            />
          </div>
          <div className={classes.btnPrivacyPolicy} onClick={this.onClickPrivacyPolicy}>Please check Privacy Policy</div>
          <div className={classes.login}>
            <span className={classes.loginLabel}>Already have an account?</span>
            <Link className={classes.loginLink} to={LOGIN_PATH}>
              Login
            </Link>
          </div>
          { this.state.isOpenPrivacyPolicy && renderPrivacyPolicy }

        {
          isLoaded(authError) && !isEmpty(authError) && snackCanOpen &&
            <Snackbar
              open={isLoaded(authError) && !isEmpty(authError) && snackCanOpen}
              message={authError ? authError.message : 'Signup error'}
              action='close'
              autoHideDuration={3000}
              onRequestClose={() => this.setState({ snackCanOpen: false })}
            />
        }
        </div>
      </div>
    )
  }
}



